import { CONNECTORSEPARATESYMBOL } from './const';

const uuidv1 = require('uuid/v1');

/**
 * @description css
 * @export {function}
 * @param {string} cssRules css
 */
export function addCssRules(cssRules) {
  const css = cssRules;
  const style = document.createElement('style');
  if (style.styleSheet) {
    style.styleSheet.cssText = css;
  } else {
    style.appendChild(document.createTextNode(css));
  }
  document.getElementsByTagName('head')[0].appendChild(style);
}

/**
 * @description DotRect
 * @param {Anchor} anchor
 * @export {function}
 * @returns {boolean}
 */
export function isEndpoint(anchor) {
  return anchor.type === 'Dot';
}

/**
 * @description uuid
 * @export {function}
 * @returns {string}
 */
export function createUuid() {
  let uuid = uuidv1();

  // uuid
  while (uuid.includes(CONNECTORSEPARATESYMBOL)) {
    uuid = uuidv1();
  }
  return uuid;
}
